













* {
  margin: 0;
  padding: 0;
}
html, body, #app, .holiday-card {
  width: 100%;
  height: 100%;
  font-family: 'Slabo 27px', serif;
}
input {
  padding: 30px;
  width: 300px;
  border-radius: 5px;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
  background-color: #eee;
  font-size: 22px;
  text-align: center;
}
input:focus {
    outline: none;
}
.holiday-card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
svg {
  width: 100%;
  height: 100%;
}
